const supportedLanguagesMap = {
  en: "en",
  no: "no",
  nb: "no",
  nn: "no",
};

let template;

class I18n {
  // https://stackoverflow.com/a/29106129
  // https://github.com/angular-translate/bower-angular-translate/blob/a0a63185d2f496c388b9c3b021405bd137045673/angular-translate.js#L478
  static getBrowserLanguages() {
    const nav = window.navigator;
    const browserLanguagePropertyKeys = [
      "language",
      "browserLanguage",
      "systemLanguage",
      "userLanguage",
    ];
    const languages = [];
    let language;
    let i;

    // support for HTML 5.1 "navigator.languages"
    if (Array.isArray(nav.languages)) {
      for (i = 0; i < nav.languages.length; i += 1) {
        language = nav.languages[i];
        if (language && language.length) {
          languages.push(language);
        }
      }
    }

    // support for other well known properties in browsers
    for (i = 0; i < browserLanguagePropertyKeys.length; i += 1) {
      language = nav[browserLanguagePropertyKeys[i]];
      if (language && language.length) {
        languages.push(language);
      }
    }

    return languages;
  }

  static init(languageForced, languageFallback) {
    let languageToFetch;
    const supportedLanguages = Object.keys(supportedLanguagesMap);

    if (languageForced) {
      if (
        languageForced.length > 2 &&
        supportedLanguages.includes(languageForced)
      ) {
        languageToFetch = languageForced;
      } else if (supportedLanguages.includes(languageForced.substring(0, 2))) {
        languageToFetch = languageForced.substring(0, 2);
      }
    } else {
      I18n.getBrowserLanguages().some((lang) => {
        if (lang.length > 2 && supportedLanguages.includes(lang)) {
          languageToFetch = lang;
          return true;
        }
        if (supportedLanguages.includes(lang.substring(0, 2))) {
          languageToFetch = lang.substring(0, 2);
          return true;
        }
        return false;
      });
    }

    if (!languageToFetch) {
      if (languageFallback) {
        if (
          languageFallback.length > 2 &&
          supportedLanguages.includes(languageFallback)
        ) {
          languageToFetch = languageFallback;
        } else if (
          supportedLanguages.includes(languageFallback.substring(0, 2))
        ) {
          languageToFetch = languageFallback.substring(0, 2);
        }
      }
    }

    if (!languageToFetch) {
      languageToFetch = "en";
    }

    return import(
      `./templates/${supportedLanguagesMap[languageToFetch]}.json`
    ).then((fetchedTemplate) => {
      template = fetchedTemplate;
      return template;
    });
  }

  static t(key) {
    if (!template) {
      throw new Error("i18n has not been initialized");
    }
    return key.split(".").reduce((p, c) => p?.[c] || null, template);
  }
}

export default I18n;
